import React from "react";
import PropTypes from "prop-types";
import { Grid, Typography, isWidthUp, withWidth } from "@material-ui/core";


import ShoppingBasketIcon from '@material-ui/icons/ShoppingBasket';
import PinDropIcon from '@material-ui/icons/PinDrop';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';

import calculateSpacing from "./calculateSpacing";
import FeatureCard from "./FeatureCard";

const iconSize = 60;

const features = [
  {
    color: "#00C853",
    headline: "Send",
    text:
      "Send urgent items with overnight delivery by verified passengers going that way.",
    icon: <ShoppingBasketIcon style={{ fontSize: iconSize }} />,
    mdDelay: "0",
    smDelay: "0"
  },
  {
    color: "#6200EA",
    headline: "Carry",
    text:
      "Carry small packages on your travel and earn fabulous rewards. Start carrying packages now.",
    icon: <ShoppingCartIcon style={{ fontSize: iconSize }} />,
    mdDelay: "200",
    smDelay: "200"
  },
  {
    color: "#61dbfb",
    headline: "Order",
    text:
      "Order something from nearby city and get it delivered overnight by verified passengers travelling your way.",
    icon: <PinDropIcon style={{ fontSize: iconSize }} />,
    mdDelay: "200",
    smDelay: "200"
  },
  
];

function FeatureSection(props) {
  const { width } = props;
  return (
    <div style={{ backgroundColor: "#FFFFFF" }}>
      <div className="container-fluid lg-p-top">
        <Typography variant="h3" align="center" className="lg-mg-bottom">
          Features
        </Typography>
        <div className="container-fluid">
          <Grid container spacing={calculateSpacing(width)}>
            {features.map(element => (
              <Grid
                item
                xs={6}
                md={4}
                data-aos="zoom-in-up"
                data-aos-delay={
                  isWidthUp("md", width) ? element.mdDelay : element.smDelay
                }
                key={element.headline}
              >
                <FeatureCard
                  Icon={element.icon}
                  color={element.color}
                  headline={element.headline}
                  text={element.text}
                />
              </Grid>
            ))}
          </Grid>
        </div>
      </div>
    </div>
  );
}

FeatureSection.propTypes = {
  width: PropTypes.string.isRequired
};

export default withWidth()(FeatureSection);
